body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.apurini-language-selector {
  min-width: 8rem;
  font-family: 'Lato', sans-serif;
  text-transform: uppercase;
  color: #555;
  font-size: 1.2rem;
  font-weight: 400;
  margin: 0px 20px;
}

.apurini-language-selector .react-select__control {
  border: 0;
  border-radius: 0;
}

.apurini-language-selector .react-select__indicator-separator {
  display: none;
}

.apurini-language-selector .react-select__indicatorContainer {
  padding: none;
}

.apurini-language-selector .react-select__menu {
  border-radius: 0;
  position: absolute;
  width: 100%;
  z-index: 100;
}

.navigation-brand {
    font-size: 2rem;
    font-weight: bold;
    padding: 1rem 1rem !important;
}
.navigation-container {
    max-height: 10rem;
}

.navigation-link {
    font-size: 1.2rem;
}

.navigation-language-selector {
    justify-content: right;
}

.intro {
  display: table;
  width: 100%;
  padding: 0;
  background: url(/static/media/intro-bg.e247e237.jpg) center center no-repeat;
  background-color: #e5e5e5;
  background-size: cover;
  -o-background-size: cover;
}

.intro .overlay {
  background: rgba(0, 0, 0, 0.2);
}

.intro h1 {
  font-family: "Raleway", sans-serif;
  color: #fff;
  font-size: 65px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;
}

.intro h2 {
  font-family: "Raleway", sans-serif;
  color: #fff;
  font-size: 36px;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;
}

.intro h1 span {
  font-weight: 800;
  color: #5ca9fb;
}

.intro p {
  color: #fff;
  font-size: 22px;
  font-weight: 300;
  line-height: 30px;
  margin: 0 auto;
  margin-bottom: 60px;
}

header .intro-text {
  padding-top: 180px;
  padding-bottom: 120px;
  text-align: center;
}

.intro-buttons {
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
}

.onBorading-group {
  margin: 2rem 0px;
}

.login-group {
  margin: 2rem 0px;
}

.header-button {
  min-width: 6rem;
}

/* Contact Section */
#contact {
	padding: 100px 0 60px 0;
	/* background: linear-gradient(to right, #dcdcdc  0%, #ffffff 100%); */
	color: #6b6b6b;
}

#contact .section-title {
	margin-bottom: 40px;
}

#contact .section-title p {
	font-size: 16px;
}

#contact h2 {
	margin-top: 10px;
	margin-bottom: 15px;
	padding-bottom: 15px;
}

#contact .section-title h2::after {
	position: absolute;
	content: "";
	background: rgba(255, 255, 255, .3);
	height: 4px;
	width: 60px;
	bottom: 0;
	left: 30px;
}

#contact h3 {
	margin-top: 80px;
	margin-bottom: 25px;
	padding-bottom: 20px;
	font-weight: 400;
}

#contact form {
	padding-top: 20px;
}

#contact .text-danger {
	color: #cc0033;
	text-align: left;
}

#contact .btn-custom {
	margin: 30px 0;
	background: transparent;
	border: 2px solid #6b6b6b;
}

#contact .btn-custom:hover {
	color: #1f386e;
	background: #fff;
}

label {
	font-size: 12px;
	font-weight: 400;
	font-family: 'Open Sans', sans-serif;
	float: left;
}

#contact .form-control {
	display: block;
	width: 100%;
	padding: 6px 12px;
	font-size: 16px;
	line-height: 1.42857143;
	color: #444;
	background-color: #fff;
	background-image: none;
	border: 1px solid #ddd;
	border-radius: 0;
	box-shadow: none;
	transition: none;
}

#contact .form-control:focus {
	border-color: #999;
	outline: 0;
	box-shadow: transparent;
}

.form-control::-webkit-input-placeholder {
	color: #777;
}

.form-control:-moz-placeholder {
	color: #777;
}

.form-control::-moz-placeholder {
	color: #777;
}

.form-control:-ms-input-placeholder {
	color: #777;
}

#contact .contact-item {
	margin: 20px 0;
}

#contact .contact-item span {
	margin-bottom: 10px;
	/* display: block; */
}

#contact .contact-item i.fa {
	margin-right: 10px;
}

#contact .social {
	border-top: 1px solid rgba(255, 255, 255, 0.15);
	padding-top: 50px;
	margin-top: 50px;
	text-align: center;
}

#contact .social ul li {
	display: inline-block;
	margin: 0 20px;
}

#contact .social i.fa {
	font-size: 22px;
	width: 48px;
	height: 48px;
	padding: 12px 0;
	border: 2px solid #6b6b6b;
	color: #6b6b6b;
	border-radius: 50%;
	transition: all 0.3s;
}

#contact .social i.fa:hover {
	color: #608dfd;
	background: #fff;
}
