/* Contact Section */
#contact {
	padding: 100px 0 60px 0;
	/* background: linear-gradient(to right, #dcdcdc  0%, #ffffff 100%); */
	color: #6b6b6b;
}

#contact .section-title {
	margin-bottom: 40px;
}

#contact .section-title p {
	font-size: 16px;
}

#contact h2 {
	margin-top: 10px;
	margin-bottom: 15px;
	padding-bottom: 15px;
}

#contact .section-title h2::after {
	position: absolute;
	content: "";
	background: rgba(255, 255, 255, .3);
	height: 4px;
	width: 60px;
	bottom: 0;
	left: 30px;
}

#contact h3 {
	margin-top: 80px;
	margin-bottom: 25px;
	padding-bottom: 20px;
	font-weight: 400;
}

#contact form {
	padding-top: 20px;
}

#contact .text-danger {
	color: #cc0033;
	text-align: left;
}

#contact .btn-custom {
	margin: 30px 0;
	background: transparent;
	border: 2px solid #6b6b6b;
}

#contact .btn-custom:hover {
	color: #1f386e;
	background: #fff;
}

label {
	font-size: 12px;
	font-weight: 400;
	font-family: 'Open Sans', sans-serif;
	float: left;
}

#contact .form-control {
	display: block;
	width: 100%;
	padding: 6px 12px;
	font-size: 16px;
	line-height: 1.42857143;
	color: #444;
	background-color: #fff;
	background-image: none;
	border: 1px solid #ddd;
	border-radius: 0;
	-webkit-box-shadow: none;
	box-shadow: none;
	-webkit-transition: none;
	-o-transition: none;
	transition: none;
}

#contact .form-control:focus {
	border-color: #999;
	outline: 0;
	-webkit-box-shadow: transparent;
	box-shadow: transparent;
}

.form-control::-webkit-input-placeholder {
	color: #777;
}

.form-control:-moz-placeholder {
	color: #777;
}

.form-control::-moz-placeholder {
	color: #777;
}

.form-control:-ms-input-placeholder {
	color: #777;
}

#contact .contact-item {
	margin: 20px 0;
}

#contact .contact-item span {
	margin-bottom: 10px;
	/* display: block; */
}

#contact .contact-item i.fa {
	margin-right: 10px;
}

#contact .social {
	border-top: 1px solid rgba(255, 255, 255, 0.15);
	padding-top: 50px;
	margin-top: 50px;
	text-align: center;
}

#contact .social ul li {
	display: inline-block;
	margin: 0 20px;
}

#contact .social i.fa {
	font-size: 22px;
	width: 48px;
	height: 48px;
	padding: 12px 0;
	border: 2px solid #6b6b6b;
	color: #6b6b6b;
	border-radius: 50%;
	transition: all 0.3s;
}

#contact .social i.fa:hover {
	color: #608dfd;
	background: #fff;
}